import Vue from 'vue'
import Router from 'vue-router'
import { Toast } from 'vant'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      component: _=> import('#/Home')
    },
    {
      path: '/recomment',
      name: 'recomment',
      beforeEnter(to, from, next) {
        Toast('recomment加紧开发中...')
      }
    },
    {
      path: '/cart',
      name: 'cart',
      component: _=> import('#/Cart')
    },
    {
      path: '/chat',
      name: 'chat',
      beforeEnter(to, from, next) {
        Toast('接口未开放')
      }
    },
    {
      path: '/search',
      name: 'search',
      beforeEnter(to, from, next) {
        Toast('search加紧开发中...')
      }
    },
    {
      path: '/goods/:id',
      name: 'goods',
      component: _=> import('#/GoodsDetail')
    },
    {
      path: '*',
      redirect: '/home'
    }
  ]
})



