<template>
  <div id="app">
    <keep-alive>
      <router-view/>
    </keep-alive>
    <div v-show="$store.state.goods.loading" class="loading-wrap">
      <van-loading type="spinner" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      loading: true,
      active: 0
    }
  }
}
</script>

<style lang="scss">
@import '@/assets/css/util.scss';

.loading-wrap {
  z-index: 100000;
  @include pf-lt(0, 0, 100vh, 100vw);
  background-color: #fff;
  .van-loading {
    @include cy();
    text-align: center;
  }
}
</style>



