import Mock from 'mockjs'
const Random = Mock.Random

export const category = () => {
  return Mock.mock({
    'category|18': [
      {
        'name|+1': ['女装', '百货', '母婴', '手机', '鞋包', '电器', '食品', '美妆', '内衣', '电脑', '运动', '男装', '汽车', '医药', '家装', '家纺', '水果', '家具']
      },
    ],
    'list|18': [{
      'keywords|10': [
        {
          'name': '@cword(4)',
          'imgurl': Random.image('108x108', '#f3f3f3', '@cword(4)'),
        }
      ],
      'ad': Random.image('750x164', '#f3f3f3', '@cword(4)'),
      'goods|30': [
        {
          'id': '@natural',
          'name': '@cword(8,18)',
          'desc': '@cword(8,18)',
          'picurl|1-10': [Random.image('400x400', '#f3f3f3', '@cword(4)')],
          'price|99-999': 99,
          'groupprice|99-999': function() {
            return this.price * 0.95 | 0
          },
          'normalprice|99-999': function() {
            return this.price + 50
          },
          'count': '@natural(1000, 100000)',
          'tag|1-3': ['@cword(4)'],
          'tickettag|1-3': ['@cword(4)'],
          'servicetag|1-3': ['@cword(4)'],
        }
      ]
    }]
  })
}


