import { category } from '@/api/goods'

const state = {
  resouce: null,
  current: null,
  loading: true,
  cart: [],
}
const getters = {
  goods(state) {
    return state.resouce && state.resouce.list
  },
}
const mutations = {
  resouce(state, val) { state.resouce = val },
  current(state, val) { state.current = val },
  loading(state, val) { state.loading = val },
  addGoods(state, val) {
    let one = state.cart.find(e => e.id === val.id)
    if (one) {
      one.count++
    } else {
      let { id, name, picurl, price, desc, tag, normalprice } = val
      state.cart.push({ id, name, picurl, price, desc, tag, normalprice, count: 1 })
    }
  },
  removeGoods(state, val) {
    let one = state.cart.find(e => e.id === val.id)
    one.count = Math.max(one.count--, 0)
  },
  deleteGoods(state, val) {
    let i = state.cart.findIndex(e => e.id === val.id)
    state.cart.splice(i, 1)
  },
}

const actions = {
  category({ commit }) {
    commit('loading', true)
    category().then(res => {
      commit('resouce', res)
      commit('loading', false)
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions,
}


