// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'

require('@/mock')

import { Tabbar, TabbarItem, Tab,
  Tabs, Swipe, SwipeItem, Lazyload,
  Grid, GridItem, Image, Icon,
  Popup, NavBar, Loading, Skeleton,
  GoodsAction, GoodsActionIcon, GoodsActionButton,
  Card, Tag, Button } from 'vant'

Vue.use(Tab).use(Tabs).use(Tabbar).use(TabbarItem)
    .use(Swipe).use(SwipeItem).use(Lazyload).use(Image)
    .use(Grid).use(GridItem).use(Icon).use(Popup).use(NavBar)
    .use(Loading).use(Skeleton).use(Card).use(Tag).use(Button)
    .use(GoodsAction).use(GoodsActionIcon).use(GoodsActionButton)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



